.star-rating {
  overflow: hidden;
  position: relative;
  height: 1em;
  line-height: 1;
  font-size: 1em;
  width: 5.4em;
  font-family: star;
  margin-bottom: 0.7rem; }
  .star-rating::before {
    content: "\73\73\73\73\73";
    float: left;
    top: 0;
    left: 0;
    position: absolute; }
  .star-rating span {
    overflow: hidden;
    float: left;
    top: 0;
    left: 0;
    position: absolute;
    padding-top: 1.5em; }
  .star-rating span::before {
    content: "\53\53\53\53\53";
    top: 0;
    position: absolute;
    left: 0; }

#reviews .comment-list {
  margin-left: 0;
  padding-left: 0;
  list-style: none; }
  #reviews .comment-list li.comment,
  #reviews .comment-list li.review {
    margin-left: 0; }
  #reviews .comment-list .comment_container {
    display: flex;
    align-items: flex-start; }
  #reviews .comment-list .avatar {
    margin-right: 1rem;
    border-radius: 0.25rem; }
  #reviews .comment-list .comment-text {
    flex: 1; }

#reviews .stars {
  display: block;
  margin-top: 0; }
  #reviews .stars a {
    position: relative;
    height: 1em;
    width: 1em;
    text-indent: -999em;
    display: inline-block;
    text-decoration: none;
    box-shadow: none; }
    #reviews .stars a::before {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 1em;
      height: 1em;
      line-height: 1;
      font-family: EDDReviews;
      content: "\e021";
      text-indent: 0; }
    #reviews .stars a:hover ~ a::before {
      content: "\e021"; }
  #reviews .stars:hover a::before {
    content: "\e020"; }
  #reviews .stars.selected a.active::before {
    content: "\e020"; }
  #reviews .stars.selected a.active ~ a::before {
    content: "\e021"; }
  #reviews .stars.selected a:not(.active)::before {
    content: "\e020"; }
